<template>
    <div class="movies__list-item" ref="item" :class="{active: selectId == movie.id}">
        <img :src="'https://www.themoviedb.org/t/p/w300_and_h450_bestv2'+movie.poster_path" alt="">
    </div>
</template>

<script>
export default {
    props: {
        movie:  Object,
        selectId: Number
    },
    mounted() {
        this.$refs.item.parentElement.scrollBy(-this.$refs.item.parentElement.scrollWidth, 0)
    }
}
</script>