export default {
    namespaced: true,
    state: () => ({
        movie: {}
    }),
    mutations: {
        setMovie(state, movies) {
            state.movie = movies;
        },
    },
    actions: {
        async getMovie({commit}, id) {
            try {
                const lang = localStorage.lang
                const fetchMovie = await fetch(`https://api.themoviedb.org/3/movie/${id}?api_key=3823c05ac49e705360a719afaaaa7d37&language=${lang}`)
                const jsonMovie = await fetchMovie.json()
                return commit("setMovie", jsonMovie);
            } catch (e) {
                console.error(e)
            }
        }
    },
    getters: {
        movie: state =>  state.movie,
    }
}