<template>
  <div class="movies-full" :class="{ active: selectId != null }">
    <button class="movies-full__close" @click="close">
      <span class="material-icons">close</span>
    </button>
    <Transition mode="out-in" name="fullview">
      <div class="movies-full__content" v-if="selectMovie != null">
        <img
          :src="`https://www.themoviedb.org/t/p/w1920_and_h800_multi_faces/${selectMovie.backdrop_path}`"
          class="movies-full__content-background"
          alt=""
        />
        <div class="movies-full__content-data">
          <div class="movies-full__content-datamovie">
            <h2 class="movies-full__content-title">
              {{ this.selectMovie.title || this.selectMovie.name }}
            </h2>
            <p class="movies-full__content-overview">
              {{ this.selectMovie.overview }}
            </p>
            <p class="movies-full__content-info">
              <span
                >{{ this.selectMovie.release_date }}
                <span v-for="(item, index) in selectMovie.genres" :key="index"
                  >{{ item.name
                  }}<span v-if="index < selectMovie.genres.length - 1"
                    >,
                  </span></span
                ></span
              >
              <span>{{ selectMovie.runtime }}</span>
            </p>
            <div class="movies-full__content-casts">
              <CastItems :count="4" :id="selectMovie.id" :type="type" />
            </div>
            <BtnMore :id="selectMovie.id" :page="'movies'" />
          </div>
        </div>
      </div>
      <div class="movies-full__scelet" v-else>
        <div class="spinner"></div>
      </div>
    </Transition>
  </div>
</template>

<script>
import CastItems from "./casts/CatsItems.vue";
import BtnMore from "./BtnMore.vue";
export default {
  components: {
    CastItems,
    BtnMore,
  },
  props: {
    selectId: Number,
    selectMovie: Object,
    index: Number,
    type: {
        type: String,
        default: 'castfilms'
    }
  },
  methods: {
      close() {
          this.$emit('close-banner')
      }
  }
};
</script>