<template>
  <nav class="appbar" :class="{ change_color: scrollPosition > 50 }">
    <div class="container appbar__content">
      <router-link to="/"
        ><img src="../assets/images/Logo.png" alt=""
      /></router-link>
      <button class="appbar__menu" @click="openm=true">
        <span class="material-icons"> menu </span>
      </button>
      <ul class="appbar__content-list" :class="{active: openm}">
        <li class="appbar__content-langinfo menu" @click="openm=false">
          <span class="material-icons">close</span>
        </li>
        <li class="appbar__content-langinfo" @click="open">
          {{ viewLang }}
        </li>
        <ul class="appbar__content-lang" :class="{ active: lang }">
          <li class="appbar__content-item">
            <span class="appbar__content-lang_item" @click="changeLang('ru')"
              >RU</span
            >
          </li>
          <li class="appbar__content-item">
            <span class="appbar__content-lang_item" @click="changeLang('en')"
              >EN</span
            >
          </li>
        </ul>
        <li class="appbar__content-item">
          <router-link to="/" @click="openm=false" class="appbar__content-link">{{
            $t("appbar.home")
          }}</router-link>
        </li>
        <li>
          <router-link to="/movies" @click="openm=false" class="appbar__content-link">{{
            $t("appbar.films")
          }}</router-link>
        </li>
        <li>
          <router-link to="/series" @click="openm=false" class="appbar__content-link">{{
            $t("appbar.series")
          }}</router-link>
        </li>
        <li>
          <router-link to="/search" class="appbar__content-link"
            ><span class="material-icons"> search </span></router-link
          >
        </li>
      </ul>
    </div>
  </nav>
</template>

<script>
export default {
  data() {
    return {
      scrollPosition: 0,
      lang: false,
      openm: false,
    };
  },
  methods: {
    updateScroll() {
      this.scrollPosition = window.scrollY;
    },
    changeLang(lang) {
      localStorage.lang = lang;
      window.location.reload();
    },
    open() {
      this.lang = true;
    },
  },
  computed: {
    viewLang() {
      document.documentElement.lang = localStorage.lang ?? "ru";
      return localStorage.lang ?? "ru";
    },
  },
  mounted() {
    window.addEventListener("scroll", this.updateScroll);
  },
};
</script>

<style lang="scss" scoped>
.change_color {
  background: #141414;
}
</style>