<template>
  <Transition name="popular__item_transition" mode="out-in">
    <div class="popular__item" v-if="slideview == index">
      <img
        :src="
          `https://www.themoviedb.org/t/p/w1920_and_h800_multi_faces/${item.backdrop_path}`
          
        "
        class="popular__item-background"
        alt=""
      />
      <div class="popular__item-content">
        <div class="container">
          <h2 class="popular__item-title">{{ item.title }}</h2>
          <p class="popular__item-overview">{{ item.overview }}</p>
          <div class="cast">
            <CastItems :count="2" :type="'casts'" :id="item.id" :index="index" />
          </div>
          <BtnMore :id="item.id" :page="'movies'"/>

        </div>
      </div>
          <div class="popular__item-next" @click="changeSlide">
              <img class="popular__item-next_image" :src="`https://www.themoviedb.org/t/p/w1920_and_h800_multi_faces/${next.backdrop_path}`" alt="">
              <div class="popular__item-next_content">
                  <span>{{ $t('header.next') }}</span>
                  <span class="popular__item-next_title">{{next.title}}</span>
              </div>
              <div class="popular__item-next_time"></div>
          </div>
    </div>
  </Transition>
</template>

<script>
import CastItems from "../casts/CatsItems.vue";
import BtnMore from "../BtnMore.vue";
export default {
  components: {
    CastItems,
    BtnMore
  },
  props: {
    item: {
      type: Object,
      required: true,
    },
    next: {
      type: Object,
      required: true,
    },
    index: {
      type: Number,
    },
    slideview: {
      type: Number,
    },
  },
  methods: {
    changeSlide() {
      this.$emit('change-slide')
    }
  }
};
</script>