<template>
  <Transition mode="out-in" name="fullview">
    <div class="popular" v-if="populars">
      <PopulatsItem
        :item="item"
        :slideview="slideview"
        :index="index"
        :next="populars[index + 1 == populars.length ? 0 : index + 1]"
        v-for="(item, index) in populars"
        :key="index"
        @change-slide="changeSlide"
      />
    </div>
    <div class="popular center" v-else>
      <div class="spinner"></div>
    </div>
  </Transition>
</template>

<script>
import PopulatsItem from "./PopulatsItem.vue";
export default {
  components: {
    PopulatsItem,
  },
  data() {
    return {
      slideview: 0,
      timeout: null,
    };
  },
  async mounted() {
    await new Promise((res) => setTimeout(() => res("foo"), 700));
    await this.$store.dispatch("popular/getPopulars");
    this.timeout = setTimeout(this.slide, 10000);
  },
  computed: {
    populars() {
      return this.$store.getters["popular/populars"];
    },
  },
  methods: {
    slide() {
      if (this.populars.length - 1 == this.slideview) {
        this.slideview = 0;
      } else {
        this.slideview++;
      }
      this.timeout = setTimeout(this.slide, 10000);
    },
    changeSlide() {
      clearTimeout(this.timeout);
      if (this.populars.length - 1 == this.slideview) {
        this.slideview = 0;
      } else {
        this.slideview++;
      }
      this.timeout = setTimeout(this.slide, 10000);
    },
  },
};
</script>