<template>
  <router-link to="/" class="cast-item" v-for="(castitem, i) in casts" :key="i">
    <img
      v-if="castitem.profile_path"
      :src="
        'https://image.tmdb.org/t/p/w138_and_h175_face' + castitem.profile_path
      "
      alt=""
      class="cast-item__image"
    />
    <img
      v-else
      src="https://govietnamvisa.com/Content/assets/images/testimonial/no-avatar.png"
      alt=""
      class="cast-item__image"
    />
    <span class="cast-item__name">{{ castitem.name }}</span>
  </router-link>
</template>

<script>
export default {
  props: {
    id: {
      type: Number,
      required: true,
    },
    index: {
      type: Number,
    },
    count: {
      type: Number,
      default: 0
    },
    type: {
      type: String,
      default: 'casts'
    },
  },
  async mounted() {
    await this.$store.dispatch(`${this.type}/getCasts`, this.id);
  },
  computed: {
    casts() {
      let casts
      if(this.type == 'casts') {
        casts = this.$store.getters[`${this.type}/casts`][this.index];
      } else if(this.type == 'castfilms') {
        casts = this.$store.getters[`${this.type}/casts`]
      } else if(this.type == 'casttv') {
        casts = this.$store.getters[`${this.type}/casts`]
      }
      if (this.count) {
        const cast = {};
        let i = 0;
        for (const key in casts) {
          cast[key] = casts[key];
          i++;
          if (i == this.count) {
            break;
          }
        }
        return cast;
      } else {
        return casts;
      }
    },
  },
};
</script>