export default {
    namespaced: true,
    state: () => ({
        movies: null
    }),
    mutations: {
        setMovies(state, movies) {
            state.movies = movies;
        },
    },
    actions: {
        async getMovies({commit}) {
            try {
                const lang = localStorage.lang
                const fetchMovies = await fetch(`https://api.themoviedb.org/3/movie/popular?api_key=3823c05ac49e705360a719afaaaa7d37&language=${lang}&page=1`)
                const jsonMovies = await fetchMovies.json()
                const result = jsonMovies.results
                const results = result.filter((a) => a.backdrop_path != null)
                console.log(results);
                commit("setMovies", results);
            } catch (e) {
                console.error(e)
            }
        }
    },
    getters: {
        movies: state => state.movies,
    }
}