export default {
    namespaced: true,
    state: () => ({
        populars: null
    }),
    mutations: {
        setPopulars(state, populars) {
            state.populars = populars;
        },
    },
    actions: {
        async getPopulars({commit}) {
            try {
                const lang = localStorage.lang
                const fetchPopular = await fetch(`https://api.themoviedb.org/3/movie/upcoming?api_key=3823c05ac49e705360a719afaaaa7d37&language=${lang}&page=1`)
                const jsonPopular = await fetchPopular.json()
                const result = jsonPopular.results
                const results = result.filter((a) => a.backdrop_path != null)
                commit("setPopulars", results);
            } catch (e) {
                console.error(e)
            }
        }
    },
    getters: {
        populars: state => state.populars,
    }
}