export default {
    namespaced: true,
    state: () => ({
        casts: {}
    }),
    mutations: {
        setCasts(state, casts) {
            state.casts = casts;
        },
    },
    actions: {
        async getCasts({commit}, props) {
            try {
                const lang = localStorage.lang
                const fetchCasts = await fetch(`https://api.themoviedb.org/3/movie/${props}/credits?api_key=3823c05ac49e705360a719afaaaa7d37&language=${lang}`)
                const jsonCasts = await fetchCasts.json()
                commit("setCasts", jsonCasts.cast);
            } catch (e) {
                console.error(e)
            }
        }
    },
    getters: {
        casts: state => state.casts,
    }
}