<template>
  <div class="movies">
    <router-link to="/movies" class="movies__title">
      {{ title }}
      <span class="material-icons"> arrow_forward_ios </span>
    </router-link>
    <Transition mode="out-in" name="fullview">
      <div class="scroll-items" v-if="movies">
        <div
          class="scroll-items__prev"
          @click="prev"
          :class="{ view: scrollPosition > 0 }"
        >
          <span class="material-icons"> arrow_back_ios </span>
        </div>
        <div class="movies__list" ref="list">
          <MoviesView
            @click="getMovie(movies[index], index)"
            v-for="(movie, index) in movies"
            :movie="movie"
            :key="index"
            :selectId="selectId"
          />
          <router-link :to="`/${main}`" class="movies__list-item">
            <span class="material-icons"> arrow_forward_ios </span>
            {{ allbtn }}
          </router-link>
        </div>
        <div
          class="scroll-items__next"
          @click="next"
          :class="{ view: scrollPosition < wscrollPosition }"
        >
          <span class="material-icons"> arrow_forward_ios </span>
        </div>
      </div>
      <div class="scroll-items center" v-else>
        <div class="spinner"></div>
      </div>
    </Transition>
    <InfoFull
      :selectId="selectId"
      :selectMovie="selectMovie"
      :type="cast"
      :index="index"
      @close-banner="close"
    />
  </div>
</template>

<script>
import MoviesView from "./MoviesView.vue";

import InfoFull from "../InfoFull.vue";
console.log(InfoFull);
export default {
  components: {
    MoviesView,
    InfoFull,
  },
  props: {
    details: {
      type: String,
      default: "movie",
    },
    cast: {
      type: String,
      default: "castfilms",
    },
    main: {
      type: String,
      default: "movies",
    },
    title: {
      type: String,
    },
    allbtn: {
      type: String,
    },
  },
  data() {
    return {
      scrollPosition: 0,
      wscrollPosition: 0,
      selectId: null,
      selectMovie: null,
      index: null,
    };
  },
  async mounted() {
    await new Promise((res) => setTimeout(() => res("foo"), 700));
    await this.$store.dispatch(`${this.main}/getMovies`);
    await new Promise((res) => setTimeout(() => res("foo"), 700));
    this.$refs.list.addEventListener("scroll", this.updateScroll);

    this.wscrollPosition = this.$refs.list.scrollWidth - window.innerWidth;
  },
  computed: {
    movies() {
      return this.$store.getters[`${this.main}/movies`];
    },
  },
  methods: {
    updateScroll() {
      this.wscrollPosition = this.$refs.list.scrollWidth - window.innerWidth;
      this.scrollPosition = this.$refs.list.scrollLeft;
    },
    prev() {
      this.$refs.list.scrollBy((-window.innerWidth / 100) * 50, 10);
    },
    next() {
      this.$refs.list.scrollBy((window.innerWidth / 100) * 50, 10);
    },
    close() {
      this.selectId = null;
      this.selectMovie = null;
    },
    async getMovie(movie, index) {
      this.selectId = movie.id;
      this.index = index;
      this.selectMovie = null;
      await new Promise((res) => setTimeout(() => res("foo"), 700));
      await this.$store.dispatch(`${this.details}/getMovie`, this.selectId);
      this.selectMovie = this.$store.getters[`${this.details}/movie`];
      this.selectMovie.release_date =
        this.selectMovie.release_date || this.selectMovie.first_air_date;
      this.selectMovie.release_date =
        this.selectMovie.release_date.split("-")[0];
      this.selectMovie.runtime = this.selectMovie.runtime
        ? this.timeDecoder(this.selectMovie.runtime)
        : "";
    },
    timeDecoder(mins) {
      mins = mins * 60;
      let hou = Math.floor(mins / 60 / 60),
        min = Math.floor(mins / 60) - hou * 60;

      min = min < 10 ? `0${min}` : min;

      const ru = hou > 0 ? `${hou}ч ${min}м` : `${min}м`;
      const en = hou > 0 ? `${hou}h ${min}m` : `${min}m`;
      if (mins) {
        if (localStorage.lang == "ru") return ru;
        if (localStorage.lang == "en") return en;
      }
    },
  },
};
</script>